@import "prismjs.variables.scss";
/**
 * okaidia theme for JavaScript, CSS and HTML
 * Loosely based on Monokai textmate theme by http://www.monokai.nl/
 * @author ocodia
 */

code[class*="language-"],
pre[class*="language-"] {
    color: $prismjs-color;
    background: none;
    text-shadow: $prismjs-text-shadow;
    font-family: $prismjs-font-family;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    // line-height: 1.5;

    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;

    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
}

/* Code blocks */
pre[class*="language-"] {
    padding: $prismjs-padding;
    margin: 0 0 $prismjs-margin-bottom;
    overflow: auto;
    border-radius: $prismjs-border-radius;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
    background: $prismjs-background-color;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
    padding: .1em;
    border-radius: $prismjs-border-radius;
    white-space: normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
    color: $prismjs-comment-color;
}

.token.punctuation {
    color: $prismjs-color;
}

.namespace {
    opacity: .7;
}

.token.property,
.token.tag,
.token.constant,
.token.symbol,
.token.deleted {
    color: $prismjs-property-color;
}

.token.boolean,
.token.number {
    color: $prismjs-boolean-color;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
    color: $prismjs-selector-color;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string,
.token.variable {
    color: $prismjs-color;
}

.token.atrule,
.token.attr-value,
.token.function,
.token.class-name {
    color: $prismjs-atrule-color;
}

.token.keyword {
    color: $prismjs-keyword-color;
}

.token.regex,
.token.important {
    color: $prismjs-regex-color;
}

.token.important,
.token.bold {
    font-weight: bold;
}
.token.italic {
    font-style: italic;
}

.token.entity {
    cursor: help;
}
